.header {
    width: 100%;
    height: 5rem;
    padding: 0 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #252424;
}

.header h1 {
    color: white;
}

.header ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

@media (max-width: 768px) {
    .header h1 {
        font-size: 1.5rem;
    }
}
