.button {
    background-color: transparent;
    border-color: #1ad1b9;
    color: #1ad1b9;
}

.button:hover,
.button:active {
    color: white;
}

.button span {
    margin: 0 0.5rem;
}

.badge {
    background-color: #1ad1b9;
    border-radius: 30px;
    padding: 0.15rem 1.25rem;
    color: #1d1d1d;
}

@media (max-width: 768px) {
    .button {
        width: 100px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    span {
        font-size: 0.7rem;
    }
    .badge {
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        padding: 0.1rem 1rem;
    }
}
